<template>
  <div class="hello-async">
    <h1>Hello async!</h1>
    <button class="show-btn" @click="btnClcked">Normal</button>
    <button class="show-btn" @click="asyncBtnClcked">Async</button>
    <p>{{ desc }}</p>
  </div>
</template>

<script>
import Utils from '../utils'

export default {
  name: 'HelloAsync',
  data () {
    return {
      n: 0,
      desc: ''
    }
  },
  methods: {
    btnClcked () {
      console.log('[normal] This is the button. ' + this.n++)
    },
    async asyncBtnClcked () {
      let a = await this.n++
      console.log('[async] This is the button. ' + a)
    }
  },
  mounted () {
    console.log('HelloAsync[Component]')
    const MyUtils = new Utils()
    this.desc  = MyUtils.getTime()
  }
}
</script>

<style>
  .show-btn {
    width: 100px;
    height: 50px;
    line-height: 50px;
    text-align: center;
    font-size: 20px;
    padding: 0;
    margin: 0;
  }
</style>



