<template>
  <div id="chart-page">
    <div class="line-chart-wrapper">
      <line-chart></line-chart>
    </div>
    <div class="histogram-wrapper">
      <histogram></histogram>
    </div>
  </div>
</template>

<script>
import LineChart from '../components/LineChart'
import Histogram from '../components/Histogram'

export default {
  name: 'ChartPage',
  components: {
    LineChart,
    Histogram
  },
  data () {
    return {}
  },
  methods: {
  },
  mounted () {
    console.log('ChartPage [Page]')
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}
#chart-page {
  overflow-x: hidden;
}
.line-chart-wrapper {
  margin: 0 auto;
  width: 600px;
  height: 400px;
  overflow: hidden;
}
.histogram-wrapper {
  margin: 50px auto;
  width: 600px;
  height: 400px;
  overflow: hidden;
}
</style>



