<template>
  <div id="histogram">
    <ve-histogram
      :data="chartData"
      :width="chartWidth"
      :legend-visible="legendVisible"
      :tooltip-visible="legendVisible"
      :settings="chartSettings"
      :extend="chartExtend"></ve-histogram>
  </div>
</template>

<script>
export default {
  name: 'Histogram',
  components: {
    VeHistogram: window.VeHistogram
  },
  data () {
    return {
      chartData: null,
      chartWidth: '600px',
      legendVisible: false,
      chartSettings: {        
        yAxisType: ['KMB']
      },
      chartExtend: {
        series: {
          barWidth: 20,
          itemStyle: {
            // 线性渐变，前四个参数分别是 x0, y0, x2, y2, 范围从 0 - 1，相当于在图形包围盒中的百分比，如果 globalCoord 为 `true`，则该四个值是绝对的像素位置
            width: 5,
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: '#2fd3b0' // 0% 处的颜色
              }, {
                offset: 1, color: '#2fbad3' // 100% 处的颜色
              }],
              globalCoord: false // 缺省为 false
            },          
            shadowColor: 'rgba(8, 114, 158, 0.48)',
            shadowBlur: 10,
            shadowOffsetY: 4
          }
        }
      }
    }
  },
  methods: {
    fillData () {
      this.chartData = {
        columns: ['日期', '销售额'],
        rows: [
          { '日期': '1月1日', '销售额': 1234 },
          { '日期': '1月2日', '销售额': 2234 },
          { '日期': '1月3日', '销售额': 3234 },
          { '日期': '1月4日', '销售额': 4234 },
          { '日期': '1月5日', '销售额': 5234 },
          { '日期': '1月6日', '销售额': 6234 }
        ]
      }
    }
  },
  mounted () {
    console.log('Histogram [Component]')
    this.fillData()
  }
}
</script>

<style>
#histogram {

}
</style>



