import Vue from 'vue'
import Router from 'vue-router'
import HelloAsync from '@/components/HelloAsync'
import HelloWorld from '@/components/HelloWorld'
import ChartPage from '@/views/ChartPage'
import TablePage from '@/views/TablePage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'TablePage',
      component: TablePage
    },
    {
      path: '/chart',
      name: 'ChartPage',
      component: ChartPage
    },
    {
      path: '/async',
      name: 'HelloAsync',
      component: HelloAsync
    },
    {
      path: '/world',
      name: 'HelloWorld',
      component: HelloWorld
    }
  ]
})



