<template>
  <div id="table-page">
    <Table :columns="columns" :data="tableData"></Table>
  </div>
</template>

<script>
import { Table } from 'iview'

export default {
  name: 'TablePage',
  components: {
    Table
  },
  data () {
    return {
      columns: [
        {
          title: 'Name',
          key: 'name'
        }, {
          title: 'Age',
          key: 'age'
        }, {
          title: 'Address',
          key: 'address'
        }
      ],
      tableData: [
        {
          name: 'haha-16',
          age: 16,
          address: 'shenzhen 16'
        }, {
          name: 'haha-17',
          age: 17,
          address: 'shenzhen 17'
        }, {
          name: 'haha-18',
          age: 18,
          address: 'shenzhen 18'
        }
      ]
    }
  }
}
</script>


