<template>
  <div id="line-chart">
    <ve-line
      :data="chartData"
      :width="chartWidth"
      :grid="chartGrid"
      :legend-visible="legendVisible"
      :tooltip-visible="legendVisible"
      :settings="chartSettings"
      :extend="chartExtend"></ve-line>
  </div>
</template>

<script>
export default {
  name: 'LineChart',
  components: {
    VeLine: window.VeLine
  },
  data () {
    return {
      chartGrid: {
        backgroundColor: '#000000'
      },
      chartExtend: {
        yAxis: {
          axisLabel: {
            formatter: '{value} k',
            show: true,
            inside: false,
            margin: 30,
            interval: 2
          },
          axisTick: {
            interval: 2
          }
        }
      },
      chartData: null,
      chartWidth: '600px',
      legendVisible: false,
      chartSettings: {
        yAxisType: ['KMB'],
        area: true,
        lineStyle: {
          // 线性渐变，前四个参数分别是 x0, y0, x2, y2, 范围从 0 - 1，相当于在图形包围盒中的百分比，如果 globalCoord 为 `true`，则该四个值是绝对的像素位置
          width: 5,
          color: {
            type: 'linear',
            x: 0,
            y: 0,
            x2: 0,
            y2: 1,
            colorStops: [{
              offset: 0.1, color: '#14aae1' // 0% 处的颜色
            }, {
              offset: 1, color: '#2fc7c1' // 100% 处的颜色
            }],
            globalCoord: false // 缺省为 false
          },
          shadowColor: 'rgba(8, 114, 158, 0.48)',
          shadowBlur: 16,
          shadowOffsetY: 8
        },
        itemStyle: {
          color: '#2fc7c1',
          borderWidth: 8          
        },
        areaStyle: {
          // 线性渐变，前四个参数分别是 x0, y0, x2, y2, 范围从 0 - 1，相当于在图形包围盒中的百分比，如果 globalCoord 为 `true`，则该四个值是绝对的像素位置
          color: {
            type: 'linear',
            x: 0.5,
            y: 0.5,
            x2: 0.5,
            y2: 1,
            colorStops: [{
              offset: 0.5, color: '#2fc7c1' // 0% 处的颜色
            }, {
              offset: 1, color: '#ffffff' // 100% 处的颜色
            }],
            globalCoord: false // 缺省为 false
          },
          opacity: 0.1
        }
      }
    }
  },
  methods: {
    fillData () {
      this.chartData = {
        columns: ['日期', '销售额'],
        rows: [
          { '日期': '1月1日', '销售额': 123 },
          { '日期': '1月2日', '销售额': 1233 },
          { '日期': '1月3日', '销售额': 2323 },
          { '日期': '1月4日', '销售额': 4223 },
          { '日期': '1月5日', '销售额': 3223 },
          { '日期': '1月6日', '销售额': 7123 }
        ]
      }
    }
  },
  mounted () {
    console.log('LineChart [Component]')
    this.fillData()
  }
}
</script>

<style>
#line-chart {
  
}
</style>



